//6 hex directions 
const directions = [
  new Phaser.Point(1, -1), 
  new Phaser.Point(0, -1),
  new Phaser.Point(-1,  0), 
  new Phaser.Point(-1, 1), 
  new Phaser.Point(0, 1),
  new Phaser.Point(1,  0)
];

class HexMap{
  constructor(){
    this.coordHexMap = {};
  }
  
  addHex(hexSprite, coord)
  {
    this.coordHexMap[coord] = hexSprite;
  }
  
  fetchHex(coord){
    if(coord in this.coordHexMap){
      return this.coordHexMap[coord];
    }
  }
  
  fetchNeighbors(coord){
    let retArray = [];
    let neighborPoint = coord;
    for(let i = 0; i < 6; i++){
      neighborPoint = Phaser.Point.add(coord, directions[i]); 
      if(neighborPoint in this.coordHexMap){
        retArray.push(this.coordHexMap[neighborPoint]);
      }
    }
    return retArray;
  }
  
  fetchAll(){
    return Object.keys(this.coordHexMap).map(key => this.coordHexMap[key]);
  }
}

export default HexMap;